import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import CostFilter from '../Filters/costfilter';
import CuisineFilter from '../Filters/cuisinefilter';
import Sortfilter from '../Filters/Sortfilter';
import SuggestionBlock from './SuggestionLogic';

const url="https://developerfunnelrestapp1.herokuapp.com/restaurents?mealtype="
const mealurl="https://developerfunnelrestapp1.herokuapp.com/mealtype"

var limit=2
class ListingApi extends Component{
    constructor(props){
        super()
        this.state={
            hotelist:'',
            mealname:'',
            activePage: 1,
            totalNoOfItems: 1,
        }
    }
    setDataAsPerFilter(sortedData){
        console.log("in setDataAsPerFilter",sortedData)
        this.setState({hotelist:sortedData})
    }    
    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-2">
                        <CostFilter costdata={(data) => {this.setDataAsPerFilter(data)}}/>
                        <CuisineFilter cuisinedata={(data) => {this.setDataAsPerFilter(data)}}/>
                        <Sortfilter sortdata={(data) => {this.setDataAsPerFilter(data)}}/>
                    </div>
                    <div className="col-md-10">
                        <SuggestionBlock/>
                        <center>
                            <h3>Here are Option of {this.state.mealname}</h3>
                        </center>
                        <ListingDisplay listdata={this.state.hotelist} activePage={this.state.activePage} limit={limit} totalNoOfItems={this.state.totalNoOfItems}
                        pageNumber={(data) => {this.setState({activePage:data})}}/>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        var mealid=this.props.match.params.id
        sessionStorage.setItem('mealid',mealid)
        axios.get(`${mealurl}`)
        .then((response)=>{
            for(var i=0;i<response.data.length;i++){
                if(response.data[i]._id===Number(mealid)){
                    this.setState({mealname:(response.data[i].name).toUpperCase()})
                    i=response.data.length;
                }
                else{
                    this.setState({mealname:'Wrong Input'})
                }
            }
        })
        fetch(`${url}${mealid}`)
        .then((res) => res.json())
        .then((data) => {this.setState({
            hotelist: data.slice(0,data.length - 1),
             totalNoOfItems:data.length - 1
         })})
     }
 }
export default ListingApi;