import React,{Component} from 'react';
import './Search.css';
import {withRouter} from 'react-router-dom';

const lurl="https://developerfunnelrestapp1.herokuapp.com/location";
const rurl="https://developerfunnelrestapp1.herokuapp.com/restaurents?city=";

class Search extends Component{
    constructor(){
        super()
        this.state={
            location:'',
            restaurents:''
        }
    }
    //Display city data
    renderCity=(data)=>{
        if(data){
           return data.map((item)=>{
               return(
                   <option value={item.city} key={item.city}>
                       {item.name} | {item.city_name}
                   </option>
               )
           })
        }
    }
    //Display restaurent data
    renderRestaurent=(data)=>{
        if(data){
           return data.map((item)=>{
               return(
                   <option value={item._id}>
                       {item.name} | {item.locality}
                   </option>
               )
           })
        }
    }
    //load restaurent wrt city
    handleCity=(event)=>{
        const cityid=event.target.value
        const rrurl=`${rurl}${cityid}`
        fetch(rrurl,{Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({restaurents:data})})
    }
    handlerest=(event)=>{
        this.props.history.push(`/details/${event.target.value}`)
    }
    render(){
        console.log(this.props)
        console.log(this.state.location);
       
        return(
            <div className="container">
            <div className="imagecontainer ">
                <div className="logo">
                    e!
                </div>
                <div className="heading">
                    Find the best restaurents, cafes, bars
                </div>
              
                <div className="locationSelector ">
                    <select className="locationDropDown" onChange={this.handleCity}>
                        <option>-------Select City------</option>
                        {this.renderCity(this.state.location)}
                    </select>
                    <select className="locationDropDown" onChange={this.handlerest}>
                        <option>--Select Restaurents----</option>
                        {this.renderRestaurent(this.state.restaurents)}
                    </select>
                </div>
            </div>
            </div>
        )
    }
    //call city api on page load
    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({location:data})})
    }
}

    
export default withRouter(Search);